<template>
  <div class="monitor">
    <div class="tab-cont">
      <ul class="tab-title">
        <li>
          <a href="https://github.com/alibaba/druid/wiki" target="_blank" class="brand lang" langkey="">Druid Monitor</a>
        </li>
        <li v-for="(item, index) in tablists" :key="item.name" :label="item.lable" :class="{active:active==index}" @click="handleClick(item.lable, item.view, index)">{{item.lable}}</li>
        <li>
          <el-button type="primary" class="btn" plain>重置</el-button>
          <el-button type="primary" class="btn" plain>记录日志并重置</el-button>
        </li>
      </ul>
      <div class="tab-list">
        <div>{{ content }}</div>
        <component :is="currentView"></component>
      </div>
    </div>
  </div>
</template>
<script>
import index from '../../../components/tabs/index'
import dataSou from '../../../components/tabs/dataSou'
import sqlMon from '../../../components/tabs/sqlMon'
import sqlFirewall from '../../../components/tabs/sqlFirewall'
import webApp from '../../../components/tabs/webApp'
import urlMon from '../../../components/tabs/urlMon'
import sessionMon from '../../../components/tabs/sessionMon'
import springMon from '../../../components/tabs/springMon'
import jsonApi from '../../../components/tabs/jsonApi'
export default {
  data () {
    return {
      active: 0,
      tablists: [
        {view: 'index', lable: '首页'},
        {view: 'dataSou', lable: '数据源'},
        {view: 'sqlMon', lable: 'SQL监控'},
        {view: 'sqlFirewall', lable: 'SQL防火墙'},
        {view: 'webApp', lable: 'Web应用'},
        {view: 'urlMon', lable: 'URI监控'},
        {view: 'sessionMon', lable: 'Session监控'},
        {view: 'springMon', lable: 'spring监控'},
        {view: 'jsonApi', lable: 'JSON API'}
      ],
      content: '123',
      currentView: 'index'
    }
  },
  components: {
    index,
    dataSou,
    sqlMon,
    sqlFirewall,
    webApp,
    urlMon,
    sessionMon,
    springMon,
    jsonApi
  },
  // 页面加载自动运行函数
  mounted: function () {
  },
  methods: {
    // 切换栏
    handleClick (name, view, num) {
      console.log(name, num)
      this.content = name
      this.active = num
      this.currentView = view
      // for (let i in this.tablists) {
      //   console.log(this.tablists[i])
      //   if (i === num) {
      //     this.tablists[i].active = true
      //   } else {
      //     this.tablists[i].active = false
      //   }
      // }
    }
  }
}
</script>
<style lang="scss" scoped>
@import url('../../../assets/css/tableData.scss');
.monitor {
  position: relative;
  padding: 10px;
  // .tab {
  //   // text-align: center;
  // }
  .tab-cont {
    .tab-title {
      border-bottom: 1px solid #eee;
      height: 30px;
      font-size: 12px;
      li {
        a {
          font-size: 20px;
        }
        float: left;
        padding: 0 10px;
        line-height: 30px;
        cursor: pointer;
      }
      li:hover {
        color: #409EFF;
      }
    }
    .tab-list {

    }
  }
}
.btn {
  padding: 6px 15px;
}
.active {
  background-color: #d5e9ff;
  color: #409eff;
  border-radius: 4px 4px 0 0;
}
</style>



