<template>
  <div class="index">
    <h2>Stat Index <a href="#">查看JSON API</a></h2>
    <table  cellspacing="0" cellpadding="10" class="tab">
      <tr>
        <td>版本</td>
        <td>{{ tabledata[0].Version }}</td>
      </tr>
      <tr>
        <td>驱动</td>
        <td>
          <p>{{ tabledata[0].Drivers[0] }}</p>
          <p>{{ tabledata[0].Drivers[1] }}</p>
          <p>{{ tabledata[0].Drivers[2] }}</p>
        </td>
      </tr>
      <tr>
        <td>是否允许重置</td>
        <td>{{ tabledata[0].ResetEnable }}</td>
      </tr>
      <tr>
        <td>重置次数</td>
        <td>{{ tabledata[0].ResetCount }}</td>
      </tr>
      <tr>
        <td>java版本</td>
        <td>{{ tabledata[0].JavaVersion }}</td>
      </tr>
      <tr>
        <td>jvm名称</td>
        <td>{{ tabledata[0].JavaVMName }}</td>
      </tr>
      <tr>
        <td>classpath路径</td>
        <td>{{ tabledata[0].JavaClassPath }}</td>
      </tr>
      <tr>
        <td>启动时间</td>
        <td>{{ tabledata[0].StartTime }}</td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tabledata: [{
        Version: '1.1.10',
        Drivers: ['com.alibaba.druid.mock.MockDriver', 'com.mysql.cj.jdbc.Driver', 'com.alibaba.druid.proxy.DruidDriver'],
        ResetEnable: true,
        ResetCount: 0,
        JavaVersion: '1.8.0_252',
        JavaVMName: 'OpenJDK 64-Bit Server VM',
        JavaClassPath: 'modelmgr-1.0.0.jar',
        StartTime: '2020-08-01 14:10:54'
      }]
    }
  },
  methods: {
  }
}
</script>
<style lang="scss" scoped>
@import url('../../assets/css/tabs.scss');
.index {
  max-width: 1000px;
  margin: 0 auto;
  table {
    tr td:nth-child(1) {
      width: 120px;
    }
  }
}
</style>



